.s-hills-pin-wrap {
    opacity: 0;
    position: absolute;
    top: calc(50% - 418.5px);
    left: calc(50% - 650px);
    width: 900px;
    height: 837px;
    z-index: 10;
    transition: 1s ease;

    .s-hills-pin {
        position: absolute;
        &:hover {
            .area-mark {
                opacity: 1;
                transition: 0.3s ease;
            }
        }
        .area-mark {
            width: 40px;
            height: 40px;
            opacity: 0;
            transition: 0.3s ease;
            cursor: pointer;
            &:hover ~ .pin {
                background: orange;
            }
        }

        .pin {
            position: absolute;
            z-index: 50;            
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            transition: 0.5s ease;
            cursor: pointer;

            &:hover {
                .pin-title,
                .pin-icon,
                .pin-line {
                    opacity: 1;
                }
            }
            
            .pin-head {
                width: 8px;
                height: 8px;
                top: 1px;
                display: flex;
                justify-content: center;
                border-radius: 50%;
                transition: 0.3s ease;
                font-size: 10px;
                font-family: Helvetica-Bold;
                line-height: 22px;
                background: $cl-white;
                color: $cl-black;
                transition: 0.3s ease;
                background: orange;
            }

            .pin-title {
                opacity: 0;
                bottom : 2px;
                width: max-content;
                padding: 6px 0px;
                font-size: 10px;
                line-height: 14px;
                color: $cl-white;
                transition: 0.3s ease;
            }
            
            .pin-icon {
                opacity: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 36px;
                height: 36px;
                border: 1px solid $cl-white;
                border-radius: 50%;
                background: $cl-dk-background;
                transition: 0.3s ease;

                img {
                    width: 24px;
                    height: 24px;
                }
            }

            .pin-line {
                opacity: 0;
                width: 0.2px;
                background: $cl-white;
                transition: 0.3s ease;
            }

            .show-pin-head {
                background: white;
            }

            .show-pin-head,
            .show-pin-line,
            .show-pin-icon,
            .show-pin-title {
                transition: 0.3s ease;
                opacity: 0.6;
            }
        }

        .hide-pin {
            opacity: 0;

            &:hover {
                opacity: 0;
            }
        }


        // premium area
        &:nth-child(1) { 
            top: 184px;
            left: 290px;
            width: 46px;
            .pin {
                left: -2px;
                top: -180px;
                flex-direction: column-reverse;
                .pin-line {
                    height: 130px;
                }
            }
        }

        // sauna & Restaurant
        &:nth-child(2) { 
            top: 200px;
            left: 350px;
            width: 46px;
            .pin {
                left: -2px;
                top: -195px;
                flex-direction: column-reverse;
                .pin-line {
                    height: 150px;
                }
            }
        }
        // meditation garden
        &:nth-child(3) {
            top: 300px; 
            left: 380px;
            width: 37px;
            .pin {
                top: 20px;
                left: -10px;
                .pin-line {
                    height: 150px;
                }
            }
        }
    }

    .s-hills-pin-selected {
        .pin {
                .pin-head {
                    color: #191919;
                }
        }
        .area-mark {
            opacity: 1;
        }
    }
}

.show-s-hills-pin-wrap {
    opacity: 1;
}