.s-fin-pin-wrap{
    opacity: 0;
    position: absolute;
    top: calc(50% - 413.5px);
    left: calc(50% + 50px);
    width: 600px;
    height: 827px;
    z-index: 10;
    transition: 1s ease;

    .fin-zoning {
        position: absolute;
        width: 900px;
        height: 100%;
        top: 0;
        left: -300px;
        opacity: 0.2;
    }

    .masterplan-cabin {
        position: absolute;
        top: calc(50% - 413.5px);
        left: calc(50% - 300px);
        width: 600px;
        height: 827px;
        transition: 0.3s ease;
        opacity: 0;
        .masterplan-cabin-img {
            position: absolute;
            top: 0;
            opacity: 0.2;
            transition: 0.3s ease;
        }
        .masterplan-cabin-ab-img {
            top: 170px;
            left: 276px;
            width: 207px;
            height: 433px;
        }
        .masterplan-cabin-cd-img {
            top: 202px;
            left: 80px;
            width: 331px;
            height: 570px;
        }
        .masterplan-cabin-ef-img {
            top: 64px;
            left: 317px;
            width: 247px;
            height: 655px;
        }
        .focus-masterplan-cabin-img {
            opacity: 1;
        }
        .all-masterplan-cabin-img {
            opacity: 1;
        }
    }

    .show-masterplan-cabin {
        opacity: 1;
    }

    .s-fin-pin {
        position: absolute;
        &:hover {
            .area-mark {
                opacity: 1;
                transition: 0.3s ease;
            }
        }
        .area-mark {
            opacity: 0;
            transition: 0.3s ease;
            cursor: pointer;
            &:hover ~ .pin {
                background: orange;
            }
        }

        .pin {
            position: absolute;
            z-index: 50;            
            display: flex;
            align-items: center;
            transition: 0.5s ease;
            cursor: pointer;

            &:hover {
                .pin-title,
                .pin-icon,
                .pin-line {
                    opacity: 1;
                }
            }
            
            .pin-head {
                width: 8px;
                height: 8px;
                top: 1px;
                display: flex;
                justify-content: center;
                border-radius: 50%;
                transition: 0.3s ease;
                font-size: 10px;
                font-family: Helvetica-Bold;
                line-height: 22px;
                background: orange;
                color: $cl-black;
                transition: 0.3s ease;
            }

            .pin-title {
                opacity: 0;
                bottom : 2px;
                width: max-content;
                padding: 0 6px;
                font-size: 10px;
                line-height: 14px;
                color: $cl-white;
                transition: 0.3s ease;
            }
            
            .pin-icon {
                opacity: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 36px;
                height: 36px;
                border: 0.4px solid $cl-white;
                border-radius: 50%;
                background: $cl-dk-background;
                transition: 0.3s ease;

                img {
                    width: 24px;
                    height: 24px;
                }
            }

            .pin-line {
                opacity: 0;
                height: 0.2px;
                background: $cl-white;
                transition: 0.3s ease;
            }

            .show-pin-head {
                background: $cl-white;
            }

            .show-pin-head,
            .show-pin-line,
            .show-pin-icon,
            .show-pin-title {
                transition: 0.3s ease;
                opacity: 0.6;
            }
        }

        .hide-pin {
            opacity: 0;

            &:hover {
                opacity: 0;
            }
        }
        
        // premium area
        &:nth-child(1) { 
            top: 80px;
            left: 373.7px;
            width: 46px;
            .pin {
                top: 4px;
                left: 20px;
                .pin-line {
                    width: 240px;
                }
            }
        }

        // sauna & Restaurant
        &:nth-child(2) { 
            top: 181px;
            left: 391.5px;
            width: 46px;
            .pin {
                left: 14px;
                .pin-line {
                    width: 227px;
                }
            }
        }
        // meditation garden
        &:nth-child(3) { 
            top: 251.5px;
            left: 288px;
            width: 37px;
            z-index: 1;
            .pin {
                top: 0px;
                right: 14px;
                flex-direction: row-reverse;
                .pin-line {
                    width: 362px;
                }
            }
        }

        // cocktail & organic tea garden
        &:nth-child(4) {
            top: 261px;
            left: 382px; 
            width: 72px;
            .pin {
                top: 32px;
                left: 24px;
                .pin-line {
                    width: 226px;
                }
            }
        }

        // forest garden
        &:nth-child(5) { 
            top: 242px; 
            left: 216px; 
            width: 135px;
            .pin {
                top: 110px;
                left: 80px;
                .pin-line {
                    width: 336px;
                }
            }
        }

        // village & bbq deck
        &:nth-child(6) { 
            top: 335px;
            left: 227PX;
            width: 57px;
            .pin {
                flex-direction: row-reverse;
                top: 30px;
                right: 26px;
                .pin-line {
                    width: 309px;
                }
            }
        }

        // main centre
        &:nth-child(7) { 
            top: 414px; 
            left: 236px;
            width: 31px;
            .pin {
                flex-direction: row-reverse;
                right: 12px;
                .pin-line {
                    width: 306px;
                }
            }
        }

        // play ground
        &:nth-child(8) { 
            top: 378px; 
            left: 131px; 
            width: 125px;
            .pin {
                flex-direction: row-reverse;
                top: 90px;
                right: 34px;
                .pin-line {
                    width: 273px;
                }
            }
        }

        // cocktail & organic tea garden
        &:nth-child(9) { 
            top: 380px; 
            left: 434px;
            width: 156px;
            .pin {
                top: 16px;
                left: 34px;
                .pin-line {
                    width: 164px;
                }
            }
        }
        
        // pet area
        &:nth-child(10) { 
            top: 535px;
            left: 242px;
            width: 122px;
            .pin {
                top: 120px;
                left: 42px;
                .pin-line {
                    width: 348px;
                }
            }
        }

        // furnicular
        &:nth-child(11) { 
            top: 570px; 
            left: 183px; 
            width: 27px;
            .pin {
                flex-direction: row-reverse;
                top: -4px;
                right: 12px;
                .pin-line {
                    width: 250px;
                }
            }
        }
    }

    .s-fin-pin-selected {
        .pin {
                .pin-head {
                    background: orange;
                    color: #191919;
                }
        }

        .area-mark {
            opacity: 1;
        }
    }
}

